
import * as types from './types'



export const saveUser = (data) => {
    return {
        type: types.SAVE_USER,
        data ,
    
    }
}

export const getUser = () => {
    return {
        type: types.GET_USER, 
    }
}

export const getUserById = (id) => {
    return {
        type: types.GET_USER_BY_ID, 
        id
    }
}

export const deleteUser = (id) => {
    return {
        type: types.DELETE_USER, 
        id,
    }
}

