class InputRangeFinal extends Component {
  constructor(props) {
    super(props);

    this.state = {
      form: {
        age: { min: 30, max: 60 },
        tuoiKetThucKeHoachNghiHuu: 90,
        soTienTietKiemMoiNam: 20,
        tiSuatLoiNhuanDuKien: 10,
        taiKhoanDauTuHuuTriBanDau: 200,
        kieuMongMuonLoiNhuan: "PHAN_TRAM",
        mongMuonRutRa: 4,
        lamPhatTruocNghiHuu: 0,
        lamPhatSauNghiHuu: 0,
        transactionId: Math.random()
          .toString(36)
          .substring(3)
      },
      mongMuonPhanTram: 4,
      mongMuonCoDinh: 20,
      tiSuatLoiNhuanDuKienMax: 100,
      taiKhoanDauTuHuuTriBanDauMax: 10000,
      soTienTietKiemMoiNamMax: 300,
      mongMuonPhanTramMax: 100,
      mongMuonCoDinhMax: 1000
    };
  }

  componentDidMount() {
    const {
      form: {
        transactionId,
        age,
        tuoiKetThucKeHoachNghiHuu,
        soTienTietKiemMoiNam,
        tiSuatLoiNhuanDuKien,
        taiKhoanDauTuHuuTriBanDau,
        kieuMongMuonLoiNhuan,
        mongMuonRutRa,
        lamPhatTruocNghiHuu,
        lamPhatSauNghiHuu
      }
    } = this.state;
    const clone_form = {
      transactionId,
      currentAge: age.min,
      tuoiNghiHuu: age.max,
      tuoiKetThucKeHoachNghiHuu,
      soTienTietKiemMoiNam: soTienTietKiemMoiNam * 1000000 * 12,
      tiSuatLoiNhuanDuKien,
      taiKhoanDauTuHuuTriBanDau: taiKhoanDauTuHuuTriBanDau * 1000000,
      kieuMongMuonLoiNhuan,
      mongMuonRutRa: mongMuonRutRa,
      lamPhatTruocNghiHuu: lamPhatTruocNghiHuu,
      lamPhatSauNghiHuu: lamPhatSauNghiHuu,
    };
    this.props.tinhHuuTri(clone_form);
  }



  handleInputRange = (value, name) => {
    const { form } = this.state;
    const { age } = form;
    if (name === "tuoiKetThucKeHoachNghiHuu" && form.age.max > value) return 1;
    if ((name === "age") && (form.tuoiKetThucKeHoachNghiHuu < value.max)) return 1;
    if (value.min < 16) return 1;
    this.setState({
      form: {
        ...form,
        [name]: value
      }
    });


  };

  handleRadio = e => {
    const { value, name } = e.target;
    const { form } = this.state;
    this.setState(
      {
        form: {
          ...form,
          kieuMongMuonLoiNhuan: value
        }
      },
      () => this.submit()
    );
  };

  formatNumber = num =>
    num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");

  submit = () => {
    const {
      form: {
        transactionId,
        age,
        tuoiKetThucKeHoachNghiHuu,
        soTienTietKiemMoiNam,
        tiSuatLoiNhuanDuKien,
        taiKhoanDauTuHuuTriBanDau,
        kieuMongMuonLoiNhuan,
        mongMuonRutRa
      }
    } = this.state;
    const { mongMuonCoDinh, mongMuonPhanTram } = this.state;
    const { lamPhatTruocNghiHuu, lamPhatSauNghiHuu } = this.props.form_cuoiKy;
    const clone_form = {
      transactionId,
      currentAge: age.min,
      tuoiNghiHuu: age.max,
      tuoiKetThucKeHoachNghiHuu,
      soTienTietKiemMoiNam: soTienTietKiemMoiNam * 1000000 * 12,
      tiSuatLoiNhuanDuKien,
      taiKhoanDauTuHuuTriBanDau: taiKhoanDauTuHuuTriBanDau * 1000000,
      kieuMongMuonLoiNhuan,
      mongMuonRutRa:
        kieuMongMuonLoiNhuan === "CO_DINH"
          ? mongMuonCoDinh * 1000000 * 12
          : mongMuonPhanTram,
      lamPhatTruocNghiHuu,
      lamPhatSauNghiHuu
    };
    this.props.tinhHuuTri(clone_form);
  };

  reSetInput = () => {
    const { form } = this.state;
    this.setState({
      form: {
        ...form,
        age: { min: 30, max: 60 },
        tuoiKetThucKeHoachNghiHuu: 90,
        soTienTietKiemMoiNam: 20,
        tiSuatLoiNhuanDuKien: 5,
        taiKhoanDauTuHuuTriBanDau: 200,
        kieuMongMuonLoiNhuan: "PHAN_TRAM",
        mongMuonRutRa: 20
      },
      mongMuonPhanTram: 20,
      mongMuonCoDinh: 20
    });
  };

  handleKieuLoiNhuan = (value, name) => {
    const { form } = this.state;
    this.setState(
      {
        form: {
          ...form,
          mongMuonRutRa: value
        },
        [name]: value
      },
      () => this.submit()
    );
  };

  handleClickDown1 = (value, name) => {
    const { form } = this.state;

    if (value > 0) {
      this.setState(
        {
          form: {
            ...form,
            [name]: value - 1
          }
        },
        () => this.submit()
      );
    }
  }

  handleClickUp1 = (value, name, nameMax) => {
    const { form } = this.state;
    if (value < this.state[nameMax]) {
      this.setState(
        {
          form: {
            ...form,
            [name]: value + 1
          }
        },
        () => this.submit()
      );
    }
  }

  handleClickDownKieuRutRa = (value, name) => {
    const { form } = this.state;
    if (value > 0) {
      this.setState(
        {
          form: {
            ...form,
            mongMuonRutRa: value - 1
          },
          [name]: value - 1
        },
        () => this.submit()
      );
    }
  }

  handleClickUpKieuRutRa = (value, name, nameMax) => {
    const { form } = this.state;
    if (value < this.state[nameMax]) {
      this.setState(
        {
          form: {
            ...form,
            mongMuonRutRa: value + 1
          },
          [name]: value + 1
        },
        () => this.submit()
      );
    }
  }

  render() {
    const { form } = this.state;
    return (
      <ContainerInputRange>
        <InputRangeAge className="slider-age">
          <TitleAge>
            Tôi <Span>{form.age.min}</Span> tuổi dự định nghỉ hưu khi{" "}
            <Span>{form.age.max}</Span> tuổi.
          </TitleAge>

          <InputRange
            maxValue={120}
            minValue={0}
            value={form.age}
            onChange={value => this.handleInputRange(value, "age")}
            onChangeComplete={this.submit}
          />
        </InputRangeAge>
        <InputRangeAge className="slider-age">
          <TitleAge>
            Tôi muốn lập kế hoạch đến năm{" "}
            <Span>{form.tuoiKetThucKeHoachNghiHuu}</Span> tuổi.
          </TitleAge>
          <InputRange
            maxValue={120}
            minValue={0}
            value={form.tuoiKetThucKeHoachNghiHuu}
            onChange={value =>
              this.handleInputRange(value, "tuoiKetThucKeHoachNghiHuu")
            }
            onChangeComplete={this.submit}
          />
        </InputRangeAge>
        <InputRangeAge className="wrapper-input">
          <TitleAge>
            Tôi dự định tiết kiệm <Span>{form.soTienTietKiemMoiNam} triệu</Span> /tháng cho
            hưu trí.
          </TitleAge>
          <i className="fa fa-caret-left icon-down" aria-hidden="true"
            onClick={() => this.handleClickDown1(form.soTienTietKiemMoiNam, 'soTienTietKiemMoiNam')}>
          </i>
          <InputRange
            maxValue={this.state.soTienTietKiemMoiNamMax}
            minValue={0}
            step={5}
            formatLabel={value => `${value} tr`}
            value={form.soTienTietKiemMoiNam}
            onChange={value =>
              this.handleInputRange(value, "soTienTietKiemMoiNam")
            }
            onChangeComplete={this.submit}
          />
          <i className="fa fa-caret-right icon-up" aria-hidden="true"
            onClick={() => this.handleClickUp1(form.soTienTietKiemMoiNam, 'soTienTietKiemMoiNam', 'soTienTietKiemMoiNamMax')}>
          </i>
        </InputRangeAge>
        <InputRangeAge className="wrapper-input">
          <TitleAge>
            Tôi đã có{" "}
            <Span>
              {this.formatNumber(form.taiKhoanDauTuHuuTriBanDau)} triệu
            </Span>{" "}
            cho tiết kiệm hưu trí.
          </TitleAge>
          <i className="fa fa-caret-left icon-down" aria-hidden="true"
            onClick={() => this.handleClickDown1(form.taiKhoanDauTuHuuTriBanDau, 'taiKhoanDauTuHuuTriBanDau')}>
          </i>
          <InputRange
            maxValue={this.state.taiKhoanDauTuHuuTriBanDauMax}
            minValue={0}
            step={10}
            value={form.taiKhoanDauTuHuuTriBanDau}
            formatLabel={value => `${value} tr`}
            onChange={value =>
              this.handleInputRange(value, "taiKhoanDauTuHuuTriBanDau")
            }
            onChangeComplete={this.submit}
          />
          <i className="fa fa-caret-right icon-up" aria-hidden="true"
            onClick={() => this.handleClickUp1(form.taiKhoanDauTuHuuTriBanDau, 'taiKhoanDauTuHuuTriBanDau', 'taiKhoanDauTuHuuTriBanDauMax')}>
          </i>
        </InputRangeAge>
        <TitleAge style={{ marginBottom: 0 }}>Mong muốn thu nhập khi nghỉ hưu của tôi:</TitleAge>
        <ContainerRadio>
          <Radio className="radio">
            <LabelRadio htmlFor="tieuhet">
              <InputRadio
                type="radio"
                id="tieuhet"
                name="kieu_loi_nhuan"
                value="PHAN_TRAM"
                checked={form.kieuMongMuonLoiNhuan === "PHAN_TRAM"}
                onChange={e => this.handleRadio(e)}
              />
              <span></span>
              <div className="input-radio">
                mỗi năm nhận <Span>{this.state.mongMuonPhanTram}%</Span> danh mục đầu tư
              </div>
            </LabelRadio>
          </Radio>

          <Radio className="radio">
            <LabelRadio htmlFor="codinh">
              <InputRadio
                type="radio"
                id="codinh"
                name="kieu_co_dinh"
                value="CO_DINH"
                checked={form.kieuMongMuonLoiNhuan === "CO_DINH"}
                onChange={e => this.handleRadio(e)}
              />
              <span></span>
              <div className="input-radio">
                mỗi tháng nhận <Span>{this.state.mongMuonCoDinh} triệu</Span>
              </div>
            </LabelRadio>
          </Radio>
        </ContainerRadio>
        {form.kieuMongMuonLoiNhuan === "CO_DINH" && (
          <div style={{ marginTop: 25 }} className="wrapper-input">
            <i className="fa fa-caret-left icon-down" aria-hidden="true"
              onClick={() => this.handleClickDownKieuRutRa(this.state.mongMuonCoDinh, 'mongMuonCoDinh')}>
            </i>
            <InputRange
              maxValue={this.state.mongMuonCoDinhMax}
              minValue={0}
              step={5}
              formatLabel={value => `${value} tr`}
              value={this.state.mongMuonCoDinh}
              onChange={value =>
                this.handleKieuLoiNhuan(value, "mongMuonCoDinh")
              }
              onChangeComplete={this.submit}
            />
            <i className="fa fa-caret-right icon-up" aria-hidden="true"
              onClick={() => this.handleClickUpKieuRutRa(this.state.mongMuonCoDinh, 'mongMuonCoDinh', 'mongMuonCoDinhMax')}>
            </i>
          </div>
        )}

        {form.kieuMongMuonLoiNhuan === "PHAN_TRAM" && (
          <div style={{ marginTop: 25 }} className="wrapper-input">
            <i className="fa fa-caret-left icon-down" aria-hidden="true"
              onClick={() => this.handleClickDownKieuRutRa(this.state.mongMuonPhanTram, 'mongMuonPhanTram')}>
            </i>
            <InputRange
              maxValue={this.state.mongMuonPhanTramMax}
              minValue={0}
              formatLabel={value => `${value}%`}
              value={this.state.mongMuonPhanTram}
              onChange={value =>
                this.handleKieuLoiNhuan(value, "mongMuonPhanTram")
              }
              onChangeComplete={this.submit}
            />
            <i className="fa fa-caret-right icon-up" aria-hidden="true"
              onClick={() => this.handleClickUpKieuRutRa(this.state.mongMuonPhanTram, 'mongMuonPhanTram', 'mongMuonPhanTramMax')}>
            </i>
          </div>
        )}

        <InputRangeAge className="wrapper-input">
          <TitleAge>Tỷ suất lợi nhuận dự kiến từ danh mục đầu tư là <Span>{form.tiSuatLoiNhuanDuKien}%</Span> /năm.</TitleAge>
          <i className="fa fa-caret-left icon-down" aria-hidden="true"
            onClick={() => this.handleClickDown1(form.tiSuatLoiNhuanDuKien, 'tiSuatLoiNhuanDuKien')}>
          </i>
          <InputRange
            maxValue={this.state.tiSuatLoiNhuanDuKienMax}
            minValue={0}
            formatLabel={value => `${value}%`}
            value={form.tiSuatLoiNhuanDuKien}
            onChange={value =>
              this.handleInputRange(value, "tiSuatLoiNhuanDuKien")
            }
            onChangeComplete={this.submit}
          />
          <i className="fa fa-caret-right icon-up" aria-hidden="true"
            onClick={() => this.handleClickUp1(form.tiSuatLoiNhuanDuKien, 'tiSuatLoiNhuanDuKien', 'tiSuatLoiNhuanDuKienMax')}>
          </i>
        </InputRangeAge>
        {/* <ContainerButton>
          <ButtonReset className="button" onClick={this.reSetInput}>
            <TextReset>Reset</TextReset>
          </ButtonReset>
          <ButtonTinhToan className="button">
            <TextReset>Tính toán mức độ khả thi</TextReset>
          </ButtonTinhToan>
        </ContainerButton> */}
      </ContainerInputRange>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  tinhHuuTri: form => dispatch(tinhHuuTri(form))
});

const mapStateToProps = ({ huuTriReducer: { form_cuoiKy } }) => {
  return {
    form_cuoiKy
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(InputRangeFinal);
