const Table = styled.table`
    border-collapse: collapse;
    width: 100%;
`;

const Row = styled.tr`
    &:nth-child(even) {
        background-color:${theme.colors.greyDesign};
    }
    background-color:${(props) => props.hilight} !important;
    ${Media.sm`
        .hidden {
            display:none;
        }
    `};
     

`;

const WrapBody = styled.tbody`
  
`;

const Col = styled.td`

    padding: 6px;
    text-align: center;
    border-right: 4px solid ${theme.colors.white};
    height: 50px;
    color:#3d3d3d;
    font-size: 18px;
    font-weight: 400;
    ${Media.sm`
        font-size: 14px;
    `};
   
`;

const WrapperTable = styled.div`
    padding: 10px;
    overflow-x:auto;
    margin-top:20px;
`;

const TitleTable = styled.p`
    color:${theme.colors.titleTable};
    height: 50px;
    font-size: 32px;
    font-weight: 900;
    ${Media.sm`
        font-size: 1.25em;
    `};
`;

