export const logPageView = () => {
  console.log(`Logging pageview for ${window.location.pathname}`)
  tracker.set({ page: window.location.pathname })
  tracker.pageview(window.location.pathname)
}
export const trackPageView = path => {
  console.log('GA', path);
  if (tracker) {
    tracker.pageview(path);
  }
};

export const trackError = (category, action, error) => {
  console.log(error, "error");
  const message = getMessageError(error);
  console.log(message, " message ");
  const labelGA = `${message} - ${error}`;
  console.log(labelGA, " labelGA ");
  // const labelGA = `${message} - ${getErrorData(error)}`;
  tracker.event({ category, action, label: labelGA });
};
export const logException = (description = '', fatal = false) => {
  if (description) {
    tracker.exception({ description, fatal })
  }
}
export const set = item => {
  tracker.set(item);
};
