import React, { Component } from "react";
import styled from "styled-components";
import Theme from "../utils/theme";

const feedBackUrl = "https://forms.office.com/Pages/ResponsePage.aspx?id=3XdYIFJ7oEKGlgfL1j3g9DffpC9mGtpEgZZMw8HXAplUREVRNUJJSUZOMUlFWVRVM0c1V0Q4S0JPRy4u";

const Tooltip = styled.div`
  /* width: 190px; */
  width: max-content;
  position: absolute;
  bottom: 52px;
  text-align: justify;
  background-color: #484848;
  box-shadow: 0px 2px 5.8px 0.2px rgba(0, 0, 0, 0.35);
  padding: 8px;
  border-radius: 3px;
  right: -14px;
  font-family: sans-serif;
  &:after {
    content: "";
    position: absolute;
    top: 100%;
    right: 40px;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #484848 transparent transparent transparent;
  }

  @media (max-width: 480px) {
    /* width: 153px; */
    bottom: 30px;
    &:after {
        right: 25px;
    }
  }
`;
const Button = styled.a`
  width: 60px;
  height: 60px;
  border-radius: 50%;
  position: fixed;
  bottom: 30px;
  right: 20px;
  background-color: ${Theme.colors.orangeVND};
  box-shadow: 0px 4px 9.3px 0.7px rgba(0, 0, 0, 0.35);
  z-index: 1000;
  @media (max-width: 480px) {
    width: 30px;
    height: 30px;
  }
`;
const WrapIcon = styled.div`
  position: relative;
  cursor: pointer;
  display: flex;
  justify-content: center;
`;
const Icon = styled.img`
  width: 31px;
  height: 31px;
  margin: 12px 0px 0px 4px;
  @media (max-width: 480px) {
    width: 15px;
    height: 15px;
    margin: 6px 0px 0px 2px;
  }
`;
const TextTooltip = styled.span`
  font-size: 14px;
  font-weight: 300;
  color: #ffffff;
  line-height: 1.4;
  padding-left: 1px;
  @media (max-width: 480px) {
    font-size: 11px;
  }
`;

const iconFeedBack = "/static/assets/Feedback-icon.png";

class ButtonFeedBack extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showTooltip: false
        };
        this.showTooltip = this.showTooltip.bind(this);
        this.hideTooltip = this.hideTooltip.bind(this);
    }

    showTooltip() {
        this.setState({ showTooltip: true });
    }

    hideTooltip() {
        this.setState({ showTooltip: false });
    }

    render() {
        const { showTooltip } = this.state;

        return (
            <div>
                <Button
                    onMouseOver={this.showTooltip}
                    onMouseOut={this.hideTooltip}
                    href={feedBackUrl}
                    target="_blank"
                >
                    <WrapIcon>
                        <Icon src={iconFeedBack} />
                        {showTooltip ? (
                            <Tooltip>
                                <TextTooltip>Góp ý cho chúng mình nhé!</TextTooltip>
                            </Tooltip>
                        ) : null}
                    </WrapIcon>
                </Button>
            </div>
        );
    }
}

export default ButtonFeedBack;