export default function HeaderPage(props) {
  const { user, token, userInfor } = useSelector(
    ({ persisteddReducer }) => persisteddReducer
  );
  const router = useRouter();

  const { menu_user } = Config;
  const check_sreen = menu_user.filter((el) => el.route === router.pathname);
  const { hoTen } = userInfor;

  return (
    <HeaderRoot>
      <WrapperImageAndNavigation>
        <WrapImage>
          <Image src="/static/assets/logo.png" alt="Logo" />
        </WrapImage>
        <Navigation>
          <NavigationMenu user={user} token={token} />
        </Navigation>
        {/* <MobileMenu {...props}/> */}
      </WrapperImageAndNavigation>
      <WrapperNameCustomerProfile>
        {check_sreen.length !== 0 && (
          <NameOfCustomer>{`KẾ HOẠCH CỦA: ${hoTen}`}</NameOfCustomer>
        )}
      </WrapperNameCustomerProfile>
    </HeaderRoot>
  );
}
