export function checkIsServer() {
  try {
    return !window;
  } catch (e) {
    return true;
  }
}

export function getMessageError(error) {
  try {
    return error;
  } catch (e) {
    return e;
  }
}

export const replaceAll = (str, search, replacement) => {
  let newStr = '';
  if (_.isString(str)) { // maybe add a lodash test? Will not handle numbers now.
    newStr = str.split(search).join(replacement);
  }
  return newStr;
};

export function isNotNull(value) {
  return value && typeof(value) !== 'undefined';
}

export function delay(ms) {
  return new Promise(res => setTimeout(res, ms*1000)); 
}

export function formatPrice (price) {
  const price_milis = price/1000000
  return price%1000000 ? price_milis.toFixed(2).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,'):price_milis
}  

export function checkEmail(email) {
  if (!email) return "Email không được bỏ trống.";
  // eslint-disable-next-line
  const regexEmail = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  if (!email.match(regexEmail)) return "Email không đúng định dạng.";
  return "";
}