import * as types from './types'

export const tinhHuuTriThanhCong = (data) => {
    return {
        type: types.TINH_HUUTRI_THANHCONG,
        payload:data ,
    
    }
}

export const tinhHuuTri = (form) => ({
    type: types.TINH_HUUTRI,
    form
})

export const tinhHuuTriDauKy = (form) => ({
    type: types.TINH_HUUTRI_DAUKY,
    form
})

export const tinhHuuTriDauKyThanhCong = (data) => {
    return {
        type: types.TINH_HUUTRI_DAUKY_THANHCONG,
        payload:data,
    
    }
}

