import React, { Component } from "react";
import { connect } from "react-redux";
import InputRange from "react-input-range";
import styled, { ThemeProvider } from "styled-components";
import "react-input-range/lib/css/index.css";
import { getTest } from "../../redux/action/userAction";
import InputRangeFinal from "./InputRangeFinal";
import RetireChart from "./RetireChart";
import TableResult from "./tableResult";
import Media from "../../utils/media";
import RetireChartCalculation from "../RetireChartCalculation";
import ButtonFeedBack from "../ButtonFeedBack";
import LamPhatCuoiKy from "./LamPhatCuoiKy";
import Note from "../huuTriDauKy/Note";
import _ from "lodash";

const Container = styled.div`
  display: flex;
  flex-direction: row;
  @media only screen and (max-width: 480px) {
    flex-direction: column;
  }
`;

const ContainerWrap = styled.div``;

const WrapperChart = styled.div`
  flex-basis: 50%;
`;
const WrapperSlider = styled.div`
  flex-basis: 50%;
  padding-left: 12px;
  @media only screen and (max-width: 480px) {
    flex-basis: 100%;
    padding-left: 0px;
  }
`;

class Index extends Component {
  constructor(props) {
    super(props);
    this.state = {
      huutri: ""
    };
  }

  render() {
    const {
      data_huutri: { data }
    } = this.props;

    const {
      tuois,
      danhSachThuNhapSauNghiHuu,
      giaTriDauTuCuoiKy,
      soTienTietKiemMoiNam,
      message,
      ketQuaKeHoach
    } = data || {};
    const desBarChart = "Mong muốn thu nhập khi nghỉ hưu (Đơn vị: triệu đồng)";
    const desBarChart2 = "Tiết kiệm hàng năm (Đơn vị: triệu đồng)";
    const desLineChart = "Giá trị đầu tư cuối kỳ (Đơn vị: triệu đồng)";

    return (
      <ContainerWrap>
        <Container>
          <WrapperChart>
            <RetireChartCalculation
              dataLabels={tuois}
              dataOfBarChart={danhSachThuNhapSauNghiHuu}
              dataOfBarChart2={soTienTietKiemMoiNam}
              dataOfLineChart={giaTriDauTuCuoiKy}
              descOfBarChart={desBarChart}
              descOfBarChart2={desBarChart2}
              descOfLineChart={desLineChart}
              messageOfData={message}
              resultOfData={ketQuaKeHoach}
            />
          </WrapperChart>
          <WrapperSlider>
            <InputRangeFinal />
          </WrapperSlider>
        </Container>
        <TableResult />
        <LamPhatCuoiKy />
        <Note />
        <ButtonFeedBack />
      </ContainerWrap>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  tinhHuuTri: form => dispatch(tinhHuuTri(form))
});

const mapStateToProps = state => {
  return {
    data_huutri: _.get(state, "huuTriReducer.data_huutri")
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Index);
