
import { Component } from "react";
import styled from "styled-components";
import { connect } from "react-redux";
import theme from "../../utils/theme";
import { tinhHuuTri } from "../../redux/action/huuTriAction";

const ContainerWrap = styled.div`
  @media (max-width: 480px) {
    padding: 0 10px;
  }
`;

const Container = styled.div`
  display: flex;
  flex-direction: row;

  @media (max-width: 480px) {
    flex-direction: column;
  }
`;

const Wrapper = styled.div`
  flex-basis: 50%;
  margin-bottom: 15px;
  display: flex;
`;

const Center = styled.div`
  margin: auto;
`;

const Label = styled.span`
  font-size: 20px;
  font-weight: 600;
  height: 27px;
  line-height: 1.35;
  color: #010101;

  @media (max-width: 480px) {
    font-size: 14px;
  }
`;

const Input = styled.input`
  width: 141px;
  height: 42px;
  border-radius: 10px;
  box-shadow: 0 2px 3px 0 rgba(0,0,0,0.16);
  background-color: #f5f5f5;
  border: 1px solid transparent;
  text-align: center;
  margin-left: 25px;
  font-size: 18px;
  font-weight: 600;
  color: #0d4f85;
  -moz-appearance: textfield;

  /* Chrome, Safari, Edge, Opera */
  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  @media (max-width: 480px) {
    width: 45px;
    margin-left: 5px;
  }

`;

const Title = styled.p`
  color: ${theme.colors.titleTable};
  font-size: 28px;
  height: 36px;
  line-height: 1.38;
  font-weight: bold;
  margin-bottom: 10px;

  @media (max-width: 480px) {
    font-size: 1.25em;
  }
`;

class LamPhatCuoiKy extends Component {
  constructor(props) {
    super(props);

    this.state = {
      form: {
        age: { min: 30, max: 60 },
        tuoiKetThucKeHoachNghiHuu: 90,
        soTienTietKiemMoiNam: 20,
        tiSuatLoiNhuanDuKien: 10,
        taiKhoanDauTuHuuTriBanDau: 200,
        kieuMongMuonLoiNhuan: "PHAN_TRAM",
        mongMuonRutRa: 4,
        lamPhatTruocNghiHuu: 0,
        lamPhatSauNghiHuu: 0,
        transactionId: Math.random()
          .toString(36)
          .substring(3)
      },
      valueMax: 100,
      valueMin: 0
    }
  }

  handleChange = (name, e) => {
    const formStore = this.props.form_cuoiKy;
    let { value } = e.target;
    value = parseInt(value);
    const { valueMin, valueMax } = this.state;
    if (value < valueMin) {
      value = valueMin;
    }
    if (value > valueMax) {
      value = valueMax;
    }
    if (value) {
      this.setState(
        {
          form: {
            ...formStore,
            [name]: parseInt(value)
          }
        },
        () => this.submit()
      )
    }
  }

  submit = () => {
    const { form } = this.state;
    this.props.tinhHuuTri(form);
  }

  render() {
    return (
      <React.Fragment>
        {this.props.form_cuoiKy.kieuMongMuonLoiNhuan !== "PHAN_TRAM" ?
          <ContainerWrap>
            <Title>Giả thiết: </Title>
            <Container>
              <Wrapper>
                <Center>
                  <Label>Tiết kiệm hằng năm tăng theo lạm phát:</Label>
                  <Input
                    type="text"
                    value={`${this.state.form.lamPhatTruocNghiHuu} %`}
                    onChange={e => this.handleChange('lamPhatTruocNghiHuu', e)}
                  >
                  </Input>
                </Center>
              </Wrapper>
              <Wrapper>
                <Center>
                  <Label>Lương hưu hằng năm tăng theo lạm phát:</Label>
                  <Input
                    type="text"
                    value={`${this.state.form.lamPhatSauNghiHuu} %`}
                    onChange={e => this.handleChange('lamPhatSauNghiHuu', e)}
                  >
                  </Input>
                </Center>
              </Wrapper>
            </Container>
          </ContainerWrap>
          : null}
      </React.Fragment>
    )
  }
}


const mapStateToProps = ({ huuTriReducer: { form_cuoiKy } }) => {
  return {
    form_cuoiKy
  };
};

const mapDispatchToProps = dispatch => ({
  tinhHuuTri: form => dispatch(tinhHuuTri(form))
});

export default connect(mapStateToProps, mapDispatchToProps)(LamPhatCuoiKy);