import React, { useState, useEffect } from 'react';
import Layout from '../components/MyLayout.js'
import Page from '../components/Page'
import Index from '../components/finalcalculation'
import { useRouter } from 'next/router'
import { useDispatch, useSelector } from 'react-redux'
import  { AUTH_PERSIST,AUTH_LOGIN } from '../redux/action/types'
import TabSubMenuHuuTri from "../components/TabSubMenuHuuTri"


const  finalCalculationPage = () => {
    
    const router = useRouter()
    const dispatch = useDispatch()
    const token_url = router.query['token-id'] 
    const token = token_url || ''

    useEffect(() => {
        // Always do navigations after the first render
        // router.push('/?counter=10', null, { shallow: true })
        if(token !== '') dispatch({ type: AUTH_LOGIN, token })
        
    }, [])

    return (
        <Layout>
            <Page>
                <TabSubMenuHuuTri screen_huuTri="final" />
                <Index />
            </Page>
        </Layout>
        
    );
    
    
}



// export async function getStaticProps() {
//     return {props:{}}
// }





export default finalCalculationPage;