import React, { Component } from "react";
import styled from "styled-components";
import { Bar } from "react-chartjs-2";
import _ from "lodash";
import theme from "../utils/theme";

const ContainerRetireChart = styled.div`
  display: flex;
  border-radius: 16px;
  padding: 20px 1px 1px 1px;
  height: 100%;
  background-color: white;
  flex: 1;
  outline: none;
  border: none;
  box-shadow: 0 0 11px rgba(33, 33, 33, 0.2);
  transition: box-shadow 0.3s;
`;
const WrapperRetireChartAndDescription = styled.div`
  display: flex;
  flex: 1;
  border-radius: 16px;
  flex-direction: column;
  background-color: white;
`;
const WrapperText = styled.div`
  display: flex;
  flex-direction: row;
  margin: 36px 0px;
  padding: 0px 24px;
  align-items: center;
  @media (max-width: 480px) {
    margin: 18px 0px;
    padding: 0px 12px;
  }
`;
const IconResult = styled.img`
  width: 80px;
  height: 80px;
  @media (max-width: 480px) {
    width: 40px;
    height: 40px;
  }
`;
const TextResult = styled.span`
  font-size: 20px;
  font-weight: 600;
  max-width: 600px;
  color: ${theme.colors.blueDesign};
  text-align: left;
  margin-left: 32px;
  @media (max-width: 480px) {
    margin-left: 16px;
    font-size: 12px;
  }
`;
const WrapperRetireChart = styled.div`
  display: flex;
  flex: 1;
  border-radius: 16px;
  background-color: ${theme.colors.white};
  padding: 16px;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
  @media (max-width: 480px) {
    padding: 16px 0px 4px 0px;
  }
`;
const NameRetireChart = styled.span`
  font-size: 13px;
  font-weight: 600;
  color: #1a1818;
  text-align: left;
  margin-bottom: 20px;
  margin-left: 6px;
  @media (max-width: 480px) {
    font-size: 12px;
  }
`;

const options = {
  responsive: true,
  legend: {
    position: "bottom",
    labels: {
      boxWidth: 20,
      labels: {
        fontColor: "black"
      }
    }
  },
  tooltips: {
    mode: "label"
  },
  elements: {
    line: {
      fill: false
    },
    point: { radius: 1 }
  },
  scales: {
    xAxes: [
      {
        position: "bottom",
        display: true,
        gridLines: {
          display: false
        },
        barPercentage: 0.4,
        beginAtZero: true,
        ticks: {
          autoSkip: true,
          fontSize: 10
        }
      }
    ],
    yAxes: [
      {
        type: "linear",
        display: true,
        position: "left",
        id: "y-axis-1",
        gridLines: {
          display: true
        },
        labels: {
          show: true
        },
        ticks: {
          beginAtZero: true,
          fontColor: "#f77e0b"
        }
      },
      {
        type: "linear",
        display: true,
        position: "right",
        id: "y-axis-2",
        gridLines: {
          display: false
        },
        labels: {
          show: true
        },
        ticks: {
          beginAtZero: true,
          fontColor: "#707070"
        }
      }
    ]
  }
};

const RetireChartCalculation = props => {
  const { messageOfData, resultOfData } = props;

  function getData() {
    const {
      descOfBarChart,
      descOfBarChart2,
      descOfLineChart,
      dataLabels,
      dataOfBarChart,
      dataOfBarChart2,
      dataOfLineChart
    } = props;

    return {
      labels: dataLabels,
      datasets: [
        {
          type: "line",
          yAxisID: "y-axis-1",
          label: descOfLineChart,
          borderWidth: 1,
          borderColor: "#f77e0b",
          pointBorderColor: "#f77e0b",
          pointBackgroundColor: "#f77e0b",
          pointHoverBackgroundColor: "#f77e0b",
          pointHoverBorderColor: "#f77e0b",
          data: dataOfLineChart
        },
        {
          type: "bar",
          yAxisID: "y-axis-2",
          label: descOfBarChart,
          borderWidth: 1,
          backgroundColor: "rgb(112, 112, 112, 0.4)",
          borderColor: "rgb(112, 112, 112, 0.4)",
          hoverBackgroundColor: "#707070",
          hoverBorderColor: "#707070",
          data: dataOfBarChart
        },
        {
          type: "bar",
          yAxisID: "y-axis-2",
          label: descOfBarChart2,
          borderWidth: 1,
          backgroundColor: "rgb(145,162,87, 0.6)",
          borderColor: "rgb(145,162,87, 0.6)",
          hoverBackgroundColor: "#91A257",
          hoverBorderColor: "#91A257",
          data: dataOfBarChart2
        }
      ]
    };
  }

  return (
    <ContainerRetireChart>
      <WrapperRetireChartAndDescription>
        {!_.isEmpty(messageOfData) ? (
          <WrapperText>
            <IconResult
              src={
                resultOfData === "SUCCESS"
                  ? "/static/assets/icon-result-output.png"
                  : "/static/assets/icon-result-fail-output.png"
              }
            />
            <TextResult>{messageOfData}</TextResult>
          </WrapperText>
        ) : null}
        <WrapperRetireChart>
          <NameRetireChart>Giá trị tài sản theo thời gian</NameRetireChart>
          <Bar data={getData()} options={options} />
        </WrapperRetireChart>
      </WrapperRetireChartAndDescription>
    </ContainerRetireChart>
  );
};

export default RetireChartCalculation;
