import React, { Component } from "react";
import styled from "styled-components";
import { connect } from "react-redux";
import { Bar } from "react-chartjs-2";
import _ from "lodash";
import theme from "../../utils/theme";

const ContainerRetireChart = styled.div`
  display: flex;
  border-radius: 6px;
  padding: 20px 1px 1px 1px;
  height: 100%;
  background-color: white;
  flex: 1;
  outline: none;
  border: none;
  box-shadow: 0 0 11px rgba(33, 33, 33, 0.2);
  transition: box-shadow 0.3s;
`;
const WrapperRetireChartAndDescription = styled.div`
  display: flex;
  flex: 1;
  border-radius: 6px;
  flex-direction: column;
  background-color: white;
`;
const WrapperText = styled.div`
  display: flex;
  flex-direction: row;
  margin: 36px 0px;
  padding: 0px 24px;
  align-items: center;
  @media (max-width: 480px) {
    margin: 18px 0px;
    padding: 0px 12px;
  }
`;
const IconResult = styled.img`
  width: 80px;
  height: 80px;
  @media (max-width: 480px) {
    width: 40px;
    height: 40px;
  }
`;
const TextResult = styled.span`
  font-size: 20px;
  font-weight: 600;
  max-width: 600px;
  color: ${theme.colors.blueDesign};
  text-align: left;
  margin-left: 32px;
  @media (max-width: 480px) {
    margin-left: 16px;
    font-size: 12px;
  }
`;
const WrapperRetireChart = styled.div`
  display: flex;
  flex: 1;
  border-radius: 6px;
  background-color: ${theme.colors.white};
  padding: 16px;
  align-items: center;
  justify-content: center;
  @media (max-width: 480px) {
    padding: 16px 0px 4px 0px;
  }
`;

const options = {
  responsive: true,
  legend: {
    position: "bottom",
    labels: {
      boxWidth: 20,
      labels: {
        // This more specific font property overrides the global property
        fontColor: "black"
      }
    }
  },
  tooltips: {
    mode: "label"
  },
  elements: {
    line: {
      fill: false
    },
    point: { radius: 1 }
  },
  scales: {
    xAxes: [
      {
        position: "bottom",
        display: true,
        gridLines: {
          display: false
        },
        barPercentage: 0.4,
        beginAtZero: true,
        ticks: {
          autoSkip: true,
          fontSize: 10
        }
      }
    ],
    yAxes: [
      {
        type: "linear",
        display: true,
        position: "left",
        id: "y-axis-1",
        gridLines: {
          display: true
        },
        labels: {
          show: true
        },
        ticks: {
          beginAtZero: true,
          fontColor: "#F4BB60"
        }
      },
      {
        type: "linear",
        display: true,
        position: "right",
        id: "y-axis-2",
        gridLines: {
          display: false
        },
        labels: {
          show: true
        },
        ticks: {
          beginAtZero: true,
          fontColor: "#707070"
        }
      }
    ]
  }
};

class RetireChart extends Component {
  getData() {
    const { data_huutri } = this.props;
    const {
      data: { tuois, danhSachThuNhapSauNghiHuu, giaTriDauTuCuoiKy }
    } = data_huutri || {};

    return {
      labels: tuois,
      datasets: [
        {
          type: "bar",
          yAxisID: "y-axis-2",
          label: "Mong muốn thu nhập khi nghỉ hưu (Đơn vị: triệu đồng)",
          borderWidth: 30,
          backgroundColor: "rgb(112, 112, 112, 0.4)",
          borderColor: "rgb(112, 112, 112, 0.4)",
          hoverBackgroundColor: "#707070",
          hoverBorderColor: "#707070",
          data: danhSachThuNhapSauNghiHuu
        },
        {
          type: "line",
          yAxisID: "y-axis-1",
          label: "Giá trị đầu tư cuối kỳ (Đơn vị: triệu đồng)",
          borderWidth: 30,
          borderColor: "#F4BB60",
          pointBorderColor: "#F4BB60",
          pointBackgroundColor: "#F4BB60",
          pointHoverBackgroundColor: "#F4BB60",
          pointHoverBorderColor: "#F4BB60",
          data: giaTriDauTuCuoiKy
        }
      ]
    };
  }

  render() {
    const { data_huutri } = this.props;
    
    const { message, ketQuaKeHoach } = data_huutri.data || {};
    
    return (
      <ContainerRetireChart>
        <WrapperRetireChartAndDescription>
          {!_.isEmpty(message) ? (
            <WrapperText>
              <IconResult
                src={
                  ketQuaKeHoach === "SUCCESS"
                    ? "/static/assets/icon-result-output.png"
                    : "/static/assets/icon-result-fail-output.png"
                }
              />
              <TextResult>{message}</TextResult>
            </WrapperText>
          ) : null}
          <WrapperRetireChart>
            <Bar data={this.getData()} options={options} />
          </WrapperRetireChart>
        </WrapperRetireChartAndDescription>
      </ContainerRetireChart>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  tinhHuuTri: form => dispatch(tinhHuuTri(form))
});

const mapStateToProps = state => {
  return {
    data_huutri: _.get(state, "huuTriReducer.data_huutri")
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(RetireChart);


